  export function bubbling(arr){
   var array = arr;
   //var temp = 0;
   for (var i = 0; i <array.length; i++){
   for (var j = 0; j <array.length - i; j++){
   if (array[j] > array[j + 1]){
       var temp=array[j + 1];
     //  temp = array[j + 1];
       array[j + 1] = array[j];
       array[j]=temp;
                         }
               }
            }
            return array;
         }

