                 <p class="com_width97">sj07,1000-30,3000-100,,!</p>
                 <p class="com_width98">1259.00</p>
                 </router-link>
             </div>
             <div class="nav_article">
                    <router-link to="/details?lid=2">
                   <img :src="`${host}img/index/content/02.jpg`" alt="">
                   <h2 class="h2conm">V20  8GB+128GB  4G </h2>
                   <p class="com_width97">sj07,1000-30,3000-100,,!</p>
                   <p class="com_width98">457.00</p>
                   </router-link>
               </div>
               <div class="nav_article">
                    <router-link to="/details?lid=3">
                   <img :src="`${host}img/index/content/03.jpg`" alt="">
                   <h2 class="h2conm">V20  8GB+128GB  4G </h2>
                   <p class="com_width97">sj07,1000-30,3000-100,,!</p>
                   <p class="com_width98">359.00</p>
                   </router-link>
               </div>
               <div class="nav_article">
                    <router-link to="/details?lid=4">
                   <img :src="`${host}img/index/content/04.jpg`" alt="">
                   <h2 class="h2conm">V20  8GB+128GB  4G </h2>
                   <p class="com_width97">sj07,1000-30,3000-100,,!</p>
                   <p class="com_width98">139.00</p>
                   </router-link>
               </div>
            </div>
            <div class="cartsts">
            <p><span v-text="`${total}`"></span></p>
            <p>{{$store.getters.getCartCount}}</p>
            <router-link to="/order">
             <img :src="`${this.host}img/cart/btm_cart.png`" class="cart_imgurl" @click="www"/>
            </router-link>
            </div>
    </div>
</template>

<script>
import Headstd from "../../components/header/Headstd"
import Sport from "../../components/sport/Sport"
export default {
    data(){
        // sessionres.send()sessionStorage
        return {
            host:this.host, //
            conter:'[-]',
            cartWidth:8.8,    //scss
            cartlength:0,      //
            islogin:false,
            uname:"ddingding",   //
            shopping:[] ,       //
            uid:this.$store.getters.getuid,  //
            total:0,      //
            flag:true
        }
    },
    components:{
        "headstd":Headstd,
        "sport":Sport
    },
    methods:{
         zhuxiao(){   // ief
            this.$messagebox.confirm("").then(action=>{
                this.$store.commit("LOGOUT");
               this.$router.push("/Index");
                 // this.islogin=false
             }).catch(err=>{
                 console.log(err); //  catch()
             });
              },
        logins(){   //
              this.islogin=true;
        },
        add(){  //
           this.flag=!this.flag; 
           if(this.flag){
               this.conter='[-]';
           }else{
              this.conter='[+]';
           }
        }
        ,
        www(){  //
            this.axios.get('details/',{params:{lid:1}}).then(res=>{
                console.log(res);
            });
        },
        getcart(){  //
          //var id=this.$store.getters.getCartCount;
             this.axios.get('shopping/cart',{params:{'id':this.uid}}).then(res=>{
                 this.shopping=res.data.data; 
                 this.cartlength=res.data.data.length;  //
                 sessionStorage.setItem('counts',this.cartlength);
                 var obj=res.data.data;//res.data.data;
               //  console.log(obj.length);
               //  console.log(Object.prototype.toString.call(obj)==="[object Aarry]");
               //  console.log({}.toString.call(obj)==="[object Aarry]");
                 for(var i=0,num=0;i<obj.length;i++){
                  num+=obj[i].price*obj[i].numcart;
                 }
               this.total=num;
                 /*
              var  row=res.data.data;
                 for(var item of row){
                    //cb checked
                    item.cb=false;
                }
                 this.open=row;
                 this.code=res.data.code;
                console.log(res);*/
             }).catch(err=>{console.log(err)})
        },
        delItem(e){    //
             //1.id
            var lid=e.target.dataset.lid;
             //2.
     this.$messagebox.confirm("").then(action=>{
          //4.ajax
          var url="shopping/delItem";
          var obj={lid:lid};
           this.axios.get(url,{params:obj}).then(result=>{
           //    console.log(result);
               this.getcart();
               this.$store.commit("clear"); 
               if(result.data.code==1){
                this.shopping=this.shopping.slice(1);
               }
           });
                       }).catch(err=>{
              // console.log(err);
           });
             //3."
            
             //5. 
         }
    },
    mounted() {
       
    },
    created() {
        this.getcart();
        //this.$store.commit("updateCount"); 

    },
}
</script>

<style lang="scss" scoped>
@import "./cart.scss"
