//6.vuex  vuex  store   storevue
import Vue from 'vue'
import Vuex from "vuex"
Vue.use(Vuex)
var mystore = new Vuex.Store({
  state:{  //       
     // 
  islogin: false,
  // ,avatarname,username
  LoginedUser: {
   phone: '',              //id 
   code: '',
   id: ''
  },
    //
    cartCount:0
    },
  mutations:{ //
    // 2
  // ???
  // 
  LOGIN (state) {
    // 
    state.islogin = true
    // sessionStorage
    let user = JSON.parse(sessionStorage.getItem('user'))
    // 
    state.LoginedUser.phone = user.phone
    state.LoginedUser.code = user.code
    state.LoginedUser.id = user.id
   },
   // 
   LOGOUT (state) {            //
    // 
    state.islogin = false
    state.LoginedUser.phone = ''
    state.LoginedUser.code = ''
    state.LoginedUser.id = ''
   },
   // 1
   increment(state){
    state.cartCount++;
   },
   clear(state){
    state.cartCount--;
   },
  updateCount(state,c){
    let num =sessionStorage.getItem('counts')
    state.cartCount+=parseInt(num);
  }
  //  5

  },
  getters:{  //             id 
      logging:function(state){  //       //       
        return state.islogin;        
      },
      getphone:function(state){  //       //       
        return state.LoginedUser.phone;        
      }, 
      getuid:function(state){
        return state.LoginedUser.id;
      },
      getCartCount:function(state){        //      
        return state.cartCount;        
      }
  },
  //
  actions:{  //  //  mutations:{ //
     
  },
  modules:{  // 
    
  }
})
export default (mystore)