      }
    },
    methods: {
      //
      brinobj(index) {
        var timer = null;//timenull
        var obj = this.$refs.reverse[index];
       // console.log(obj);
        timer = setInterval(() => {// 
          var a = obj.getAttribute("data-time");
          var b = new Date().getTime();//
          var d = 0, s = 0, h = 0, m = 0;//
          var ee = obj.getElementsByTagName("em")//em
          d = Math.floor((a - b) / 1000 / 60 / 60 / 24);//
          h = Math.floor((a - b) / 1000 / 60 / 60 % 24);//
          m = Math.floor((a - b) / 1000 / 60 % 60);//
          s = Math.floor((a - b) / 1000 % 60);//
          if (d < 10) {
            d = "0" + d
          } else if (d < 0) {
            d = 0
          }
          if (h < 10) {
            h = "0" + h
          } else if (h < 0) {
            h = 0
          }
          if (m < 10) {
            m = "0" + m;
          } else if (m < 0) {
            m = 0;
          }
          if (s < 10) {
            s = "0" + s;
          } else if (s < 0) {
            s = 0;
          }
          ee[0].innerHTML = d;
          ee[1].innerHTML = h;
          ee[2].innerHTML = m;
          ee[3].innerHTML = s;
          if (a <= b) {
            clearInterval(timer);
            obj.innerHTML = "";
          }
        }, 1000)
      },

      addw() {  //
        //    console.log(111);
        axios.get('./sss.html'
        ).then(res => { console.log(res) });
      },
      // 
      getindex_item1() {
        return new Promise((open, err) => {
