      goindex() {
    this.getindex_item1().then(() => {
   this.getindex_item2().then(() => {
   this.getindex_item3().then(() => {
  this.getindex_item4().then(() => {  //updated()           
              for(var i=0;i<this.dingshiqi;i++){
                this.brinobj(i); //     
                this.brinobj(i); //
                this.brinobj(i); // 
                this.brinobj(i);
              }     
              })
            })
          })
        })
      },
      //
      //
      brinobjindex(id) {
        var timer = null;//timenull
        var obj = document.getElementById(id)//spanid
        timer = setInterval(function () {//
          var a = new Date((obj.getAttribute("data-time"))).getTime();
          //obj.getAttribute("data-time")data-time
          //new Date().getTime(),
          var b = new Date().getTime();//
          var d = 0, s = 0, h = 0, m = 0;//
          var ee = obj.getElementsByTagName("em")//em
          d = Math.floor((a - b) / 1000 / 60 / 60 / 24);//
          h = Math.floor((a - b) / 1000 / 60 / 60 % 24);//
          m = Math.floor((a - b) / 1000 / 60 % 60);//
          s = Math.floor((a - b) / 1000 % 60);//
          //100
          if (d < 10) {
            d = "0" + d
          } else if (d < 0) {
            d = 0
          }
          if (h < 10) {
            h = "0" + h
          } else if (h < 0) {
            h = 0
          }
          if (m < 10) {
            m = "0" + m;
          } else if (m < 0) {
            m = 0;
          }
          if (s < 10) {
            s = "0" + s;
          } else if (s < 0) {
            s = 0;
          }
          ee[0].innerHTML = d;
          ee[1].innerHTML = h;
          ee[2].innerHTML = m;
          ee[3].innerHTML = s;
          if (a <= b) {//0
            clearInterval(timer);
            obj.innerHTML = "";
            //    ee[0].innerHTML = 00;
            //    ee[0].innerHTML = 00;
            //   ee[1].innerHTML = 00;
            //    ee[2].innerHTML = 00;
          }
        }, 1000)
      }
