<template>
  <div>
    <headstd msg=""></headstd>
    <ul class="arrange">
      <li>
        <router-link to="#"><i id="arrange_up"></i></router-link>
      </li>
      <li>
        <router-link to="#"><i class="arrange_down"></i></router-link>
      </li>
      <li>
        <router-link to="#"><i class="arrange_discuss"></i></router-link>
      </li>
      <li>
        <router-link to="#"><i class="arrange_sales"></i></router-link>
      </li>
    </ul>
    <!-- @touchstart.stop="start"
        @touchmove.stop="move"
        @touchend.stop="end" -->
    <!--  :style="'transform:translateY('+top+'px);transition:.5s all'"-->
    <div class="content_div" ref="content_div">
      <div class="pro_box" v-for="(elem,i) of list" :key="i">
        <div class="clearfix">
          <router-link :to="elem.details">
            <img class="fl" v-lazy.container="elem.details_img" alt="Breo  ">
          </router-link>
        </div>
        <div class="pro_brief">
          <router-link :to="elem.details">
            <p v-text="elem.subtitle" class="p1"></p>
            <p><span v-text="`${elem.price}`"></span></p>
            <p v-text="`${elem.inventory_msg}`" class="p3"></p>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Headstd from "../../components/header/Headstd";  //
  import {getClientHeight,getScrollTop} from '../../../public/statis/toole'
  export default {
    data() {
      return {
        host: this.host,
        list: [],  //
        pno: 0,  //
        // pno1: 2,
        ps: 8,   //
        startY: 0,          //  y
        touching: false,   //  
        top: 0,
        endY: 0,
        startYy: 0,
        aa: 0,
        startPos: {},
        endPos: {}
      }
    },
    components: {
      "headstd": Headstd,
    },
    methods: {
      //
      /*
      getproduct(){
    var url='product/add';
   this.axios.get(url).then(res=>{  
   })
  },
  */
      start(e) {
        // ee.targetTouches[0].pageY y
        // console.log(e);
        let touch = e.targetTouches[0];
        this.startPos = { x: touch.pageX, y: touch.pageY, time: +new Date };    //touch

        let start = touch.pageY;
        this.startYy = touch.pageY
        // console.log(start);
        //this.startY = e.targetTouches[0].pageY;
        // 
        this.touching = true;

      },

      move(e) {
        //   touchMove touching
        //   touchingfalse
        if (!this.touching) return
        //  
        let diff = e.targetTouches[0].pageY - this.endY
        console.log(this.aa)
        //   
        //   this.loadMore()
        if (diff > 600) {
          e.preventDefault()
          this.loadMore().then(() => {
            // this.loadMore2();
          })
        } else {
          return
        }
        //this.toptransform: translateY(+top+ 'px')
        // diff +(this.state === 2 ? 40 : 0) 
        //  diff*0.25
        this.top = Math.floor(diff * 0.25) + (this.state === 2 ? 40 : 0)
        if (this.top >= 50) {
          this.state = 1;   //
          // this.top=20;
        } else {
          this.state = 0  // 
        }
      },

      end(e) {

        this.touching = false
        var touch = e.targetTouches[0];
        // this.endY=e.targetTouches[0].pageY;

        this.aa = this.endY - this.startYy;
        // console.log(e.targetTouches[0])
        // console.log(touch.pageY)
        if (this.state === 2) {
          this.top = 40
          return
        }
        // 40 
        if (this.top >= 50) {
          //this.refresh()

        } else {
          this.state = 0
          this.top = 0
        }
      },
      /*
      move:function(event){
                  //touchmove
                  if(event.targetTouches.length > 1 || event.scale && event.scale !== 1) return;
                  var touch = event.targetTouches[0];
                  this.endPos = {x:touch.pageX - this.startPos.x,y:touch.pageY - this.startPos.y};
                var  isScrolling = Math.abs(this.endPos.x) < Math.abs(this.endPos.y) ? 1:0;    //isScrolling10
                  if(isScrolling === 1){
                      event.preventDefault(); 
                      console.log(this.endPos.y-this.startPos.y);
      
                  }
              },
              */
      loadMore() {  //
        //this.pno;
        return new Promise((open, err) => {
          this.pno++;
          //  console.log(this.pno);
          var obj = { pno: this.pno, pageSize: this.ps };
          //5.ajax 
          var url = "product/paging";   //
          // var obj={uname:u,upwd:p};//uname
          this.axios.get(url, { params: obj }).then(result => {  //get(url,{params:obj}) params.
            //  
            // console.log(result.data.data);
            // this.list=result.data.data;
            var rows = this.list.concat(result.data.data); //1
            this.list = rows;
            open();
            //  console.log(1);       
            console.log(result.data.data);
          })
        });
      },
      /*
      loadMore2(){  //
       return new Promise((open,err)=>{ 
      //this.pno++;
      //this.pno1;
      var obj={pno:this.pno1,pageSize:this.ps};
            //5.ajax 
     var url ="product/paging";   //
    // var obj={uname:u,upwd:p};//uname
     this.axios.get(url,{params:obj}).then(result=>{  //get(url,{params:obj}) params.
     //  
      // console.log(result.data.data);
     // this.list=result.data.data;
       var rows=this.list.concat(result.data.data); //1
         this.list=rows;
         open();
         console.log(result);
       //  console.log(2);
      }) 
   }) 
      }
      */
      
      loaddibu() {
        var scrollTop = getClientHeight();
        var clientHeight = getScrollTop();
        console.log(scrollTop,clientHeight);
        var scrollHeight = document.documentElement.scrollHeight
        var loadBottom = scrollTop + clientHeight == scrollHeight;  // +==
        //console.log(loadBottom)               //
        if (loadBottom) {
          this.loadMore()
        }
      }
    },
    created: function () {
      //  this.getproduct()   //
      this.loadMore().then(() => {
        // this.loadMore2();
      })
    },
    mounted() {
      window.addEventListener('touchstart', this.start, { passive: false });
      window.addEventListener('touchmove', this.move);
      window.addEventListener('touchend', this.end);
      // console.log(document.documentElement)
      window.addEventListener("scroll", this.loaddibu)
      //   window.addEventListener('scroll', () => {
      //       console.log(window.scrollTop);
      // })
    },
    watch: {
      // console.log();
    },
  }
</script>

<style lang="scss" scoped>
  @import './Ordering.scss'
</style>